import React, {Component} from "react"
import avatar from "../assets/img/avatar.png";
import Tada from 'react-reveal/Tada';
import {Fade} from "react-reveal";
import Clouds from "./Clouds"
import {Spring} from "react-spring/renderprops-universal";
import {hideAll} from "react-reveal/src/lib/globals";



class Banner extends Component {

    constructor(props) {
        super(props)
        this.state = {
            hovered: false,
            clicked: false,
            height: "7vh",
            width: "20vw",
            showForm: false
        }
    }

    sayHi = () => {
        if (!this.state.clicked) {
            this.setState({
                clicked: true,
                height: "50vh",
                width: "30vw",
                obj: this.contactForm,
                showForm: true
            })
        } else {
            this.setState({
                clicked: false,
                showForm: true
            })
        }
        console.log("Pressed")
    }

    render() {
        return (
            <div className="banner">
                <Clouds/>
                <h1 className={"bannerHeader"}>
                    Student of Computer Science
                </h1>
                <p className="bannerInfo">
                    Any fool can write code that a computer can understand.<br/> Good programmers write code that humans
                    can understand
                </p>
                <Tada>
                    <img className={"avatar"}
                         src={avatar}
                         alt={"Simon avatar"}/>
                </Tada>
                <div className="mountain">
                    {svgMountain}
                </div>
            </div>
        );
    }

    contactForm = () => {
        return(
            <div style={{height: this.state.height, width: this.state.width}} className="sayHi" onClick={this.sayHi}>
                <form method="POST">
                    <label htmlFor="name">Name</label>
                    <input type="text" name="name"/>

                    <label htmlFor="email">Email</label>
                    <input type="email" name="email"/>

                    <label htmlFor="message">Message</label>
                    <textarea name="message" rows="3"></textarea>

                    <input type="submit"/>
                </form>
            </div>
        )
    }


    sayHiDiv = () => {
        return(
            "Say hi!"
        )
    }
}
